.dropdown {
  position: relative; }
  .dropdown__list {
    padding: 0;
    margin: 0;
    list-style: none;
    position: absolute;
    left: 0;
    right: 0;
    z-index: 10;
    top: calc(100% + 2px);
    box-shadow: 2px 2px 5px #EAEAEA;
    background-color: #FFFFFF;
    border: 1px solid #EAEAEA;
    border-radius: 6px;
    padding: 0 16px;
    max-height: 297px;
    overflow-y: auto; }
  .dropdown__list-item {
